import React, { useState, useEffect } from 'react'

import { FormControlLabel, FormControl, Radio, RadioGroup } from '@mui/material'

import { plot_color_palette } from '../../../utils/color'

import TaskPlot from '../../../components/Plot/TaskPlot'
import DistributionPlot from '../../../components/Plot/Distribution'
import Modal from 'src/feat/Modal'

import { PrimaryButton } from 'src/styled'
import { OptimizationContainer, OptimizationCharts, LoadingTitle } from './styled'

export default function BaselineSummary({ optimizer, baseline, results, selectedPreset, setSelectedPreset, selectedResultIndex, setSelectedResultIndex, goNext }) {

  const [currentTasks, setCurrentTasks] = useState(Object.values(optimizer.project.tasks).filter((i) => i.newFinish))
  const baselineTasks = Object.values(optimizer.plannedproject.tasks).filter((i) => i.newFinish)

  const listOfPresets = Object.keys(results)
  const selectedPresetResult = results[selectedPreset]

  useEffect(() => {
    setSelectedResultIndex(optimizer?.results[selectedPreset].length - 1)
  }, [])

  useEffect(() => {
    setCurrentTasks(Object.values(optimizer?.project?.tasks).filter((i) => i.newFinish))
    if (optimizer) {
      return optimizer?.simulate(selectedPresetResult?.at(selectedResultIndex).durationRatios)
    }
  }, [selectedPresetResult]) // preset changes here 

  const statData = (title, numbers) => {
    return <div className='stats'>
      <div className='title'>{title}
      </div>
      <div className='figure'>
        <div className='numbers'>{numbers}</div>
      </div>
    </div >
  }

  return <>
    <OptimizationContainer>
      <h2>Baseline Summary</h2>
      <div className='summary'>
        {statData('Project Duration (days)', Math.round(baseline?.duration))}
        {statData('Resources', Math.round(baseline?.resources?.length))}
        {statData('Tasks', Object.values(baseline?.tasks).length)}
        {statData('Tasks on Critical Path', Math.round(baseline?.critical_paths?.length))}
      </div >

      {
        results ? <>
          <OptimizationCharts className="multi-charts">
            {listOfPresets ?
              <div className='full-chart'>
                <div className='section'>
                  {results === 0 || results === undefined ? <h3><LoadingTitle><span>.</span><span>.</span><span>.</span></LoadingTitle></h3> : <h3>Tasks Overview</h3>}
                  <div className='legend'>
                    <div className='item'>
                      <div style={{ width: '14px', height: '14px', background: plot_color_palette.ComparisonPlot[selectedPreset], marginRight: '5px', borderRadius: '50%' }}></div>
                      <div>{selectedPreset}</div>
                    </div>
                    <div className='item'>
                      <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
                      <div>Baseline</div>
                    </div>
                    <div className='item'>
                      <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
                      <div>Critical Path</div>
                    </div>
                    {/*
                    {len(optimizationPresets[loadingPreset ? loadingPreset : selectedPreset].optimizer.results.at(-1)?.inp?.tasks || {}) <= 400 ?
                      <div className='item'>
                        <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
                        <div>Critical Path</div>
                      </div>
                      : <></>
                    } */}
                  </div>
                  <TaskPlot
                    tasks={currentTasks || []}
                    width='1150'
                    height='650'
                    color={plot_color_palette.ComparisonPlot[selectedPreset]}
                    baselineTasks={baselineTasks || []}
                    title='Tasks (Overview)' />
                </div>
              </div>
              : null
            }
          </OptimizationCharts>
          <OptimizationCharts>
            <div className='chart'>
              <div className='section'>
                <h3>Comparison Chart</h3>
                <div className='legend'>
                  <div className='item'>
                    <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
                    <div>Baseline</div>
                  </div>

                  {listOfPresets.map((p) => {
                    return <div key={p} className='item'>
                      <div style={{ width: '14px', height: '14px', background: plot_color_palette.ComparisonPlot[p], marginRight: '5px', borderRadius: '50%' }}></div>
                      <div>{p}</div>
                    </div>
                  })}
                </div>

                <DistributionPlot
                  results={results}
                  baselineResults={optimizer?.plannedproject}
                  steps={optimizer?.project?.settings?.optimizationSteps}
                  // optimizationPresets={optimizationPresets}
                  selectedPreset={selectedPreset}
                  selectedResultIndex={selectedResultIndex}
                  x_label='Duration (days)'
                  y_label='Maximum Resource (units)'
                  onClick={(preset, i) => {
                    setSelectedPreset(preset)
                    setSelectedResultIndex(i)
                  }}
                />
              </div>
            </div>

            <div className='chart'>
              <div className='section'>
                <h3>Optimization Presets</h3>
                <p>These available presets are pre-configured settings that are designed to deliver the best performance or quality for each individual specific use case.</p>
                <p>Please select from one of the following options to generate your optimized report: </p>
                <FormControl component='fieldset'>
                  <RadioGroup
                    value={selectedPreset}
                    onChange={(e) => {
                      setSelectedPreset(e.target.value)
                      setSelectedResultIndex(optimizer.results[e.target.value].length - 1)
                    }}
                  >
                    {listOfPresets.map((o) => {
                      return <div key={o} className='row presets'>
                        <FormControlLabel value={o} control={<Radio />} label={o} />
                        <div style={{ fontSize: '12px', fontFamily: 'Inter' }}>{o}</div>
                      </div>
                    })}
                  </RadioGroup>
                </FormControl>
                <div className='row'>
                  <div />
                  <PrimaryButton variant='contained' onClick={goNext}>
                    View Optimized Summary
                  </PrimaryButton>
                </div>
              </div>
            </div>
          </OptimizationCharts > </> : ''
      }

    </OptimizationContainer >
    {/* <Modal isLoading={isLoading} title="Error: 0 optimization steps" content={<div>We were unable to optimize your schedule any further due to strict constraints. Please try again. <a href="/">Back to homepage.</a></div>} /> */}
  </>
}